*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Face Your Fears', sans-serif;
}

body{
    display: none;
}

@media only screen and (max-width: 600px) {
    
    .horror-text {
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-name: flickering;
  animation-timing-function: step-start;
  color: #912914;
  font-size: 72px;
}

@keyframes flickering {
  0% {
    color: #912914;
    text-shadow: none;
    transform: skewX(0);
  }
  5% {
    color: #912914;
    text-shadow: none;
    transform: skewX(0);
  }
  10% {
    color: #912914;
    text-shadow: 1px 1px #eee;
    transform: skewX(0);
  }
  15% {
    color: #ccc;
    text-shadow: none;
    transform: skewX(0);
  }
  20% {
    color: #912914;
    text-shadow: none;
    transform: skewX(0);
  }
  25% {
    color: #ccc;
    text-shadow: 2px 2px #912914;
    transform: skewX(0);
  }
  30% {
    color: #ccc;
    text-shadow: 2px 2px #912914;
    transform: skewX(0);
  }
  35% {
    color: #ccc;
    text-shadow: 2px 2px #912914;
    transform: skewX(0);
  }
  37% {
    color: #ccc;
    text-shadow: 1px 1px #eee;
    transform: skewX(0);
  }
  40% {
    color: #912914;
    text-shadow: 1px 1px #eee;
    transform: skewX(0);
  }
  45% {
    color: #912914;
    text-shadow: 1px 1px #eee;
    transform: skewX(0);
  }
  50% {
    color: #912914;
    text-shadow: none;
    transform: skewX(0);
  }
  55% {
    color: #912914;
    text-shadow: none;
    transform: skewX(0);
  }
  60% {
    color: #912914;
    text-shadow: none;
    transform: skewX(0);
  }
  65% {
    color: #912914;
    text-shadow: none;
    transform: skewX(0);
  }
  68% {
    color: #912914;
    text-shadow: 1px 1px #eee;
    transform: skewX(0);
  }
  70% {
    color: #912914;
    text-shadow: 1px 1px #eee;
    transform: skewX(0);
  }
  75% {
    color: #912914;
    text-shadow: 1px 1px #eee;
    transform: skewX(0);
  }
  80% {
    color: #912914;
    text-shadow: 1px 1px #eee;
    transform: skewX(0);
  }
  85% {
    color: #912914;
    text-shadow: 1px 1px #eee;
    transform: skewX(0);
  }
  90% {
    color: #912914;
    text-shadow: none;
    transform: skewX(0);
  }
  95% {
    color: #912914;
    text-shadow: 1px 1px #eee;
    transform: skewX(0);
  }
  100% {
    color: #912914;
    text-shadow: none;
    transform: skewX(0);
  }
}


/* Additional styling */
html, body {
  background-color: #222;
  border: 0;
  height: 100%;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: relative;
  width: 100%;
}

.center {
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

    body{
        display: block;
        background-image: url(../images/Jigsaw.jpg);
        background-repeat: no-repeat;
        background-size: 100% 100vh;
        background-clip: content-box;
        background-position: center;
        width: 100vw;
        height: 100vh;
        overflow: hidden;
    }
    .container{
        font-family: 'Face Your Fears', sans-serif;
        color: crimson;
        font-size: 24px;
        text-align: center;
        display: flex;
        flex-direction: column;
        gap: 20px;
        align-items: center;
        height: 100vh;
    }
    p{
        margin-left: 20px;
        margin-right: 20px;
    }
    .firstClue{
        font-family: 'Face Your Fears', sans-serif;
        color: crimson;
        background: inherit;
        border: white 1px solid;
        padding: 10px 20px;
        font-size: 24px;
        text-align: center;
        margin-top: 30px;
    }
    .check{
        font-size: 48px;
    }
    h1{
        margin-top: 30px;
        margin-bottom: 30px;
    }
    button{
        font-family: 'Face Your Fears', sans-serif;
        color: crimson;
        padding: 10px 30px;
        background: inherit;
        border: none;
        font-size: 36px;
    }
    .yes{
        font-size: 128px;
        position: absolute;
        top: 40%;
    }
    .no{
        position: absolute;
        bottom: 17%;
        z-index: 100;
    }
    @keyframes showMe {
     0%{
        font-size: 36px;
        color: white;
        position: absolute;
        text-align: center;
        top: 10%;
     }
     25%{
        font-size: 42px;
        text-align: center;
        position: absolute;
        top: 20%;
        color: black;
     }   
     50%{
        font-size: 32px;
        position: absolute;
        top: 40%;
        color: white;
     }
     75%{
                font-size: 42px;
        position: absolute;
        top: 50%;
     }
     100%{
        font-size: 24px;
        position: absolute;
        top: 75%;
     }
    }
    .animated-no{
        animation: showMe 10s;
    }

.none{
        display: none;
    }

}

